import msdyn365Commerce, { IThemeRegistration } from '@msdyn365-commerce/core-internal';

export const getAvailableThemes = () => {
    const themeRegistration: IThemeRegistration = msdyn365Commerce.themes;
    return Object.keys(themeRegistration.themes)
        .map(themeName => {
            return {
                [themeName]: themeName
            };
        })
        .reduce((prev, curr) => ({ ...prev, ...curr }), {});
};

export const getStyles = () => {
    const themesEnum = getAvailableThemes();
    return {
        pageTheme: {
            name: 'page-theme',
            friendlyName: 'Theme',
            description: 'Available registered themes',
            enum: {
                ...themesEnum
            },
            default: '',
            scope: 'module'
        }
    };
};
